import HeadSearch from '../components/HeadSearch.vue';

export default {
  name: 'Search',
  mixins: [],
  components:{
      HeadSearch
  },
  data(){
    return {
      result: undefined,
      search: '',
      lastSearch: '',
      countResult: 0,
      maxpage: 0,
      page: 1,
      boxvisibility: true,
      showWheel: true,
      columns: ['denomination','siren','']
    }
  },
  props: {
  },
  computed: {
  },
  mounted() {
    
  },
  methods: {
    lastsearchEvent(res){
      this.lastSearch = res;
    },
    resultEvent(res){
      this.result = res;
      this.showWheel = false;
    },
    pageEvent(res){
      this.page = res;
    },
    searchEvent(res){
      this.search = res;
    },
    maxpageEvent(res){
      this.maxpage = res;
    },
    countResultEvent(res){
      this.countResult = res;
    },
    searchText(){
      console.log('search')
      console.log(this.search)
    },
    goto(id){
      this.$router.push('/organisme/'+id);
    },
    previouspage(){
      this.$refs.headsearch.previouspage()
    },
    nextpage(){
      this.$refs.headsearch.nextpage()
    },
    closeBox(){
      console.log('close')
      this.boxvisibility = false;
    }
  },
  watch:{
  },
  created(){
  },
}
